.cbsChartRectangle {
	position: relative;
	margin-bottom: $space;
	max-width: 32rem;

	&:before {
		content: '';
		display: block;
		position: relative;
		padding-bottom: (100%/1.5);
		max-height: 100vh;
	}
}

.cbsBlockTableControl {
	background-color: getColor(hemelblauw lighter);
	padding-left: 1rem;
	padding-right: 1rem;
	border-top: 1px solid getColor(hemelblauw default);
}

@mixin cbsBlockTableContainerWidthInPercentage {

	// A bunch of magic numbers in order to match chart width
	margin-left: 10.8%;
	width: 77.6%;
}

@mixin cbsBlockTableContainerWidthInRem {
	// A bunch of magic numbers in order to match chart width
	margin-left: 3.8rem;
	width: 24.5rem;
}

.cbsBlockTableContainer {
	@include cbsBlockTableContainerWidthInPercentage;
	margin-bottom: $space;

	@media (min-width: 701px) {
		@include cbsBlockTableContainerWidthInRem;
	}

	@include mqGreaterThan($threeColumnGridBreakpoint) {
		@include cbsBlockTableContainerWidthInPercentage;
	}

	@media (min-width: 1190px) {
		@include cbsBlockTableContainerWidthInRem;
	}

	@media (min-width: 1300px) {
		@include cbsBlockTableContainerWidthInPercentage;
	}

	@media (min-width: 1418px) {
		@include cbsBlockTableContainerWidthInRem;
	}
}

.cbsTable {
	position: relative;
	width: auto;
	margin: $halfSpace 0 $space 0;

	@include mqLesserThan(mediumLarge) {
		max-width: 21.2rem;
	}
}

.cbsDataCell {
	min-width: 0;
	max-width: none;
}


$cbsLoadingIndicatorSize: 2rem;

.cbsLoadingSvg {
	@include centerAbsolute($cbsLoadingIndicatorSize, $cbsLoadingIndicatorSize);
}

.cbsErrorIndicator {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.cbsErrorIndicatorText {
	background-color: getColor(violet default);
	color: getColor(wit);
	padding: 0.2rem 0.4rem;
	font-size: getFontSize(small);
}

.cbsBlockSource {
	margin: 1rem 0 0 0;
	font-size: getFontSize(small);
	text-align: right;
}

.cbsBlockSourceCCLink {
	@include noUnderline;
}

.cbsCCIcon {
	width: 0.8em;
	height: 0.8em;
	display: inline-block;
	vertical-align: -10%;
	margin-left: 0.2em;
}